.signup-input-container {
    margin: 30px auto;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 300px;
    gap: 20px;
    padding: 30px;
    border-radius: 10px;
    box-shadow: 2.5px 2.5px 2.5px 2.5px #cbd5e1;
}

.signup-form {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 20px;
}

.signup-inputs {
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    border: 1px solid #cbd5e1;
    width: 250px;
}

.signup-button {
    width: 250px;
    padding: 10px;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    color: #fff;
    background-color: #334155;
    border: 2px solid #000;
    border-radius: 5px;
}

.disabled-signup-button {
    width: 250px;
    padding: 10px;
    font-size: 16px;
    text-transform: uppercase;
    letter-spacing: 0.5px;
    border-radius: 5px;
    border: none;
}

.signup-button:hover {
    cursor: pointer;
    background-color: #000;
}

.signup-para {
    margin: 0px;
    font-size: 16px;
}

.signup-para span {
    color: #334155;
    font-weight: 700;
    cursor: pointer;
    text-transform: uppercase;
}

.signup-input-container select {
    padding: 10px;
    border-radius: 5px;
    font-size: 16px;
    border: 1px solid #cbd5e1;
    width: 270px;
    cursor: pointer;
    appearance: none;
}

